import React from "react";
import { NavLink } from "react-router-dom";

const Navigation = () => {
  return (
    <div className="navigation">
      <ul>
        <NavLink to="/">
          <li>
            <i className="fas fa-home"></i> ACCUEIL
          </li>
        </NavLink>
        <NavLink to="/nous-connaitre">
          <li>NOUS CONNAITRE </li>
        </NavLink>
        <NavLink to="/s'orienter">
          <li>S'ORIENTER</li>
        </NavLink>
        <NavLink to="/actes">
          <li>LES ACTES</li>
        </NavLink>
        <NavLink to="/urgences">
          <li>LES URGENCES</li>
        </NavLink>
      </ul>
    </div>
  );
};

export default Navigation;
