import React from "react";
import { NavLink } from "react-router-dom";

const Footer = () => {
  return (
    <div>
      <footer className="footer">
        <div className="footer-container">
          <div className="contact">
            <h2>Clinique Des Jardins</h2>
            <p>santé mentale</p>
            <button>
              <i class="fa-sharp fa-solid fa-envelope"></i>
              <NavLink to="/contactez">Nous contacter</NavLink>
            </button>
            <p>+212 05224-73453</p>
            <p>+212 05224-73614</p>
          </div>
          <div className="social">
            <a href="http://www.twitter.com">
              <li>
                <i className="fab fa-twitter"></i>
              </li>
            </a>
            <a href="http://fab fa-facebook-f">
              <li>
                <i className="fab fa-facebook-f"></i>
              </li>
            </a>
            <a href="http://www.instagram.com">
              <li>
                <i className="fab fa-instagram"></i>
              </li>
            </a>
          </div>
          <div className="country">
            <div className="img-contain">
              <img
                src="./assets/img/Maroc-drapeau.jpg"
                alt="Maroc-drapeau.jpg"
              />
            </div>

            <div className="minister-contain">
              <a href="http://https://www.sante.gov.ma/Pages/Accueil.aspx">
                <img
                  src="./assets/img/ministere  de la sante.png"
                  alt="ministere  de la sante"
                />
              </a>
            </div>
          </div>
        </div>
      </footer>
    </div>
  );
};

export default Footer;
