import React from "react";
import ReactPlayer from "react-player";

const VideoClinique = () => {
  const internalSource =
    "assets/img/Centre privé de Santé Mentale-Clinique Des Jardins-.mp4";
  return (
    <div className="video-container">
      <ReactPlayer url={internalSource} controls />
    </div>
  );
};

export default VideoClinique;
