import React from "react";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import Actes from "./pages/Actes";
import Emergency from "./pages/Emergency";
import Guide from "./pages/Guide";
import Home from "./pages/Home";
import NotFound from "./pages/NotFound";
import Weknow from "./pages/WeKnow";
import FromMessage from "./pages/FromMessage";

const App = () => {
  return (
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/nous-connaitre" element={<Weknow />} />
        <Route path="/s'orienter" element={<Guide />} />
        <Route path="/actes" element={<Actes />} />
        <Route path="/urgences" element={<Emergency />} />
        <Route path="*" element={<NotFound />} />
        <Route path="/contactez" element={<FromMessage />} />
      </Routes>
    </BrowserRouter>
  );
};

export default App;
