import React from "react";
import { NavLink } from "react-router-dom";
import Footer from "../Components/Footer";
import Header from "../Components/Header";

const Guide = () => {
  return (
    <div>
      <Header />
      <div className="down-header">
        <h1>Clinique Des Jardins</h1>
        <p>Clinique privée d'addictologie.</p>
      </div>

      <div className="navigation-container-guide">
        <ul>
          <NavLink to="/">
            <li>
              Accueil <span>&#10093;</span>
            </li>
          </NavLink>
          <NavLink to="/nous-connaitre">
            <li>
              Nous connaître <span>&#10093;</span>
            </li>
          </NavLink>
          <NavLink to="/s'orienter">
            <li>
              s'orienter <span>&#10093;</span>
            </li>
          </NavLink>
          <li>L'Hospitalisation</li>
        </ul>
      </div>

      <div className="container-guide">
        <div className="text-containner-guide">
          <div className="Hospitalisation" id="Hospitalisation">
            <h1>L'Hospitalisation</h1>
            <p>
              Lorem ipsum dolor sit amet consectetur adipisicing elit.
              Asperiores commodi dolore molestias culpa voluptatibus, maxime
              iusto magnam pariatur recusandae quia iure distinctio. Dolor
              maiores pariatur voluptatum voluptates cumque, quis asperiores!
            </p>
          </div>
          <div
            className="Hospitalisation-complete"
            id="L'Hospitalisation complete"
          >
            <div className="title-guide">
              <h3>L'Hospitalisation complete</h3>
            </div>
            <p>
              Dans le cas d'une decision d'hopspitalisation La duree definit par
              le medecin La suite quoi apres ? Suite à la decharge du patient de
              son hospitalisation plusieurs scenarios soffrent à lui. le suivi
              ambulatoire du patient, ou celui ci revient voir son medecin en
              consultation afin d'evaluerl'amelioration de son etat et de
              l'accompagner. dans certains certains cas l'hopital de jour est
              suggere par le medecin suite à l'hospitalisation du patient.
            </p>
          </div>
          <div className="hopital-de-jour" id="L'hopital de jour">
            <div className="title-guide">
              <h3>L'hopital de jour</h3>
            </div>
            <p>
              <em>L 'HOPITAL DE JOUR c'est quoi?</em> <br />
              L’hôpital de jour s’adresse à des personnes dont l’état de santé
              nécessite des soins pendant la journée, mais qui sont en capacité
              de vivre chez elles, à domicile. L’Hopital de jour constitue une
              alternative à l’hospitalisation complète permettant ainsi de
              maintenir la personne dans son environnement et de prévenir les
              périodes de crise. En fonction de leurs besoins, les personnes
              viennent une ou plusieurs demi-journées par semaine. Les personnes
              bénéficient de soins donnés par une équipe soignante (psychiatres,
              psychologues, etc...) et peuvent participer à des activités
              thérapeutiques animées par des infirmières et infirmiers ou
              éducateurs et éducatrices. Pour aller à l’HJ, la personne doit
              être adressée par sa ou son psychiatre. Dnas le cadre de l'HDJ un
              programme specifique à chaque patient est mis en place lui
              porposant des activités ciblées.
            </p>
          </div>
          <div className="Consultation" id="La Consultation">
            <div className="title-guide">
              <h3>La Consultation</h3>
            </div>
            <p>
              La Clinique Des Jardins dispose d'un service de consultation ou
              des professionels de la santé mentale parmis les plus réputés sur
              la place Casablancaise repondent à vos besoins. <br /> La
              consultation psychiatrique <br /> l'addictologue. Parler de tests
              effectue (Multi 5) <br /> La consultation psychologique
            </p>
          </div>
        </div>

        <div className="racourci-container-guide">
          <div className="raccourci">
            <h3> Nous connaître</h3>
            <ul>
              <a href="#Hospitalisation">
                <li>Hospitalisation</li>
              </a>
              <a href="#L'Hospitalisation complete">
                <li>Unitée protegée</li>
              </a>
              <a href="#L'hopital de jour">
                <li>L'hopital de jour</li>
              </a>
              <a href="#La Consultation">
                <li>La Consultation</li>
              </a>
            </ul>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default Guide;
