import React from "react";
import { useEffect } from "react";

const DynamicText = () => {
  useEffect(() => {
    const target = document.querySelector("#text-target");
    let array = [
      "Accueil, écoute, accompagnement gentillesse et les qualité professionnelles ont fait que même malade, mon séjour s'est révélé plus que positif.",

      "Un rapport très humain et sérieux avec tout le personnel, toujours à l'écoute surtout la nuit quand vous souffrez",

      " Une bonne prise en charge le chirurgien passant tous les jours dans la chambre les infirmières répondant toujours lors de l'appel",
    ];
    let wordIndex = 0;
    const createWord = () => {
      const word = document.createElement("span");
      target.appendChild(word);

      word.classList.add("word");
      word.textContent = array[wordIndex];

      setTimeout(() => {
        word.remove();
      }, 4000);
    };

    const loop = () => {
      setTimeout(() => {
        if (wordIndex >= array.length) {
          wordIndex = 0;
          loop();
        } else if (wordIndex < array.length) {
          createWord();
          wordIndex++;
          loop();
        } else {
          wordIndex = 0;
          setTimeout(loop, 5000);
        }
      }, 5000);
    };
    loop();
  }, []);

  return (
    <div className="dynamic">
      <h1 id="text-target">
        <div class="fa-3x">
          <i class="fa-solid fa-bell fa-shake"></i>
        </div>
      </h1>
    </div>
  );
};

export default DynamicText;
