import React from "react";
import { NavLink } from "react-router-dom";
import Footer from "../Components/Footer";
import Header from "../Components/Header";

const Emergency = () => {
  return (
    <div>
      <Header />
      <div className="down-header-emergency">
        <h1>Clinique Des Jardins</h1>
        <p>Clinique privée d'addictologie.</p>
      </div>

      <div className="navigation-container-emergency">
        <ul>
          <NavLink to="/">
            <li>
              Accueil <span>&#10093;</span>
            </li>
          </NavLink>
          <NavLink to="/nous-connaitre">
            <li>
              Nous connaître <span>&#10093;</span>
            </li>
          </NavLink>
          <NavLink to="/s'orienter">
            <li>
              s'orienter <span>&#10093;</span>
            </li>
          </NavLink>
          <NavLink to="/actes">
            <li>
              Les actes <span>&#10093;</span>
            </li>
          </NavLink>
          <NavLink to="/urgences">
            <li>
              Urgences <span>&#10093;</span>
            </li>
          </NavLink>
          <li>permanance</li>
        </ul>
      </div>

      <div className="container-emergency">
        <div className="text-container">
          <h1>LES URGENCES</h1>
          <p>
            Dans le cas d'une consultation non programée la clinique des jardins
            dispose d'un service d'urgence operant 24/24 et ce 7j/j sans rdv
            prealable afin d'accueilir les patiens.
          </p>
          <p>
            Dans le cas d'addiction des tests de depistage peuvent etre effectué
            au sein du service d'urgence.
          </p>
          <p>
            Dans le cas d'une hospitalisation non programee la clinique des
            jardins est dotée d'un service d'accueil des hospitalisation
            d'urgence et ce 24/24 7j/7.
          </p>
        </div>

        <div className="raccourci-container">
          <div className="raccourci">
            <h3> Nous connaître</h3>
            <ul>
              <a href="#dispo">
                <li>7J/7</li>
              </a>
              <a href="#all">
                <li>24h/24</li>
              </a>
              <a href="#prop">
                <li>Consultation non programée</li>
              </a>
            </ul>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default Emergency;
