import React from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";

import "../style/components/styles.css";

// import required modules
import { Navigation, Pagination, Mousewheel, Keyboard, Autoplay } from "swiper";
import "swiper/css/autoplay";

export default function App() {
  return (
    <>
      <Swiper
        cssMode={true}
        navigation={true}
        pagination={true}
        mousewheel={true}
        keyboard={true}
        modules={[Navigation, Pagination, Mousewheel, Keyboard, Autoplay]}
        autoplay={{ delay: 4000 }}
        className="mySwiper"
      >
        <SwiperSlide className="swiper-slide img01"></SwiperSlide>
        <SwiperSlide className="swiper-slide img02 "></SwiperSlide>
        <SwiperSlide className="swiper-slide img03 "></SwiperSlide>
        <SwiperSlide className="swiper-slide img04"></SwiperSlide>
        <SwiperSlide className="swiper-slide img05"></SwiperSlide>
        <SwiperSlide className="swiper-slide img06 "></SwiperSlide>
        <SwiperSlide className="swiper-slide img07 "></SwiperSlide>
        <SwiperSlide className="swiper-slide img07 "></SwiperSlide>
      </Swiper>
    </>
  );
}
